.border {
  border: 1px solid #ccc8cc;
}
.border-b {
  border-bottom: 1px solid #ccc8cc;
}
.border-b-2 {
  border-bottom: 4px solid #ccc8cc;
}
.border-b-3 {
  border-bottom: 10px solid #ccc8cc;
}
.border-t {
  border-top: 1px solid #ccc8cc;
}
