  border-top: 4px solid #ccc8cc;
}
.border-t-3 {
  border-top: 10px solid #ccc8cc;
}
.border-primary {
  border-color: #ba43ae;
}
.border-rounded {
  border-radius: 0;
}
hr {
  border-top: none;
  border-bottom: 1px solid #ccc8cc;
  margin: 30px 0;
}
.tab-focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
.full-height {
  min-height: 100vh;
}
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}
.invisible {
  visibility: none;
}
.faded {
  opacity: 0.4;
}
.pseudo-hidden {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.block {
  display: block;
}
.inline-block {
  display: inline-block;
}
.loader {
  position: relative;
  display: block;
}
.loader:after {
  position: absolute !important;
  display: block;
  height: 1rem;
  width: 1rem;
  top: 50%;
  left: 50%;
  margin-left: -0.5rem;
  margin-top: -0.5rem;
  border: 2px solid rgba(72, 71, 72, 0.6);
  border-radius: 0.5rem;
  border-right-color: transparent;
  border-top-color: transparent;
  animation: spin-around 500ms infinite linear;
  content: "";
}
